/* 
 * To change this template, choose Tools | Templates
 * and open the template in the editor.
 */

(function(){
    
    'use strict';

    angular.module('toolbarButtons', [])

    .directive('filterButton', ['$compile', function($compile) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button tooltip="{{tooltipText}}" class="btn btn-default btn-mini btn-toolbar filterBtn" data-toggle="dropdown">\n\
                                <a class="toolbar-icon filter"></a>\n\
                            </button>\n\
                            <ul class="dropdown-menu awk-df-form-dropdown-filter awk-df-form-dropdown pull-right" role="menu" >\n\
                                <li >\n\
                                    <form name="form" role="form" style="padding: 10px; ">\n\
                                        <div form-directive ></div>\n\
                                    </form>\n\
                                </li>\n\
                            </ul>\n\
                    </div>',
                scope: {
                    tooltipText: '@',
                    iconClass: '@',
                    fieldVal: '@',
                    submitLabel: '@',
                    clearLabel: '@',
                    submitFunc: '@',
                    clearFunc: '@'
                   
                },
                /**/
                link: function(scope, elem, attrs) {

                    //scope.$parent.iconClass = scope.iconClass;
                    //var iconBtn = elem.find('a');
                    //iconBtn..attr('class', "toolbar-icon "+iconClass);
                    var childDir = elem.find('div');
                    childDir.attr('filter-fields', scope.fieldVal);
                    childDir.attr('filter-array-name', scope.fieldVal);
                    childDir.attr('submit-label', scope.submitLabel);
                    childDir.attr('clear-label', scope.clearLabel);
                    childDir.attr('on-submit', scope.submitFunc);
                    childDir.attr('on-clear', scope.clearFunc);
                    elem.on('show.bs.dropdown', function (event) {
                        scope.$emit("filterDropDownOpened",{"target":event.currentTarget});
                    })
                   // $compile(childDir.contents())(scope.$parent);
                    $compile(elem.contents())(scope.$parent);
                }
                /**/
            };
        }])



    /*REPORT BUTTON*/

    /**
     * @ngdoc directive
     * @name reportButton
     * @module toolbarButtons
     * @description 
     * Angular directive to show the report button as a part of toolbar.
     * This directive belongs to the 'toolbarButtons' module and should be included in the app.js to be used within an application.
     * Reference to the handler function in the view's parent controller can be provided via setting the 'action' attribute.

     * @example <span report-button action="generateReportFunction()"></span>
     * @see http://docs.angularjs.org/guide/directive
     *
     */

    .directive('reportButton', ['$compile', function($compile) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button title="View report" class="btn btn-default btn-mini" data-toggle="dropdown" ng-click="action()">\n\
                                <i class="glyphicon glyphicon-stats"></i>\n\
                            </button>\n\
                            <ul class="dropdown-menu pull-right" role="menu">\n\
                                <div>\n\
                                    <img src="../app/assets/img/report.png"></img>\n\
                                 </div>\n\
                            </ul>\n\
                        </div>',
                scope: {
                    action: '&'
                },
                link: function(scope, elem, attrs) {
                    /*
                    var btnElem = elem.find('button');
                    btnElem.attr('ng-click', scope.saveFunction);
                    $compile(elem.contents())(scope.$parent);
                    */
                }
            }

        }])



    /*CREATE REPORT BUTTON*/    

    /* 
     * To change this template, choose Tools | Templates
     * and open the template in the editor.
     */


    .directive('createReportBtn', ['$compile', function($compile) {

        console.log('in new directive');
        return{
            restrict: 'A',
            replace: true,
            template: '<div class="dropdown btn-group">\n\
                        <button tooltip="{{tooltipText}}" class="btn btn-default btn-mini btn-toolbar filterBtn" data-toggle="dropdown">\n\
                            <a class="toolbar-icon createReport"></a>\n\
                        </button>\n\
                        <ul class="dropdown-menu form-dropdown-filter toolbar-dropdown pull-right" role="menu">\n\
                            <li >\n\
                                <form name="form" role="form" class="form" style="padding: 10px; ">\n\
                                    <div form-directive ></div>\n\
                                </form>\n\
                            </li>\n\
                        </ul>\n\
                </div>',
            scope: {
                tooltipText: '@',
                iconClass: '@',
                fieldVal: '@',
                submitLabel: '@',
                clearLabel: '@',
                submitFunc: '@',
                clearFunc: '@'
            },
            /**/
            link: function(scope, elem, attrs) {

                console.log('ff: ', scope.fieldVal);
                console.log('sf: ', scope.searchFunc);
                //scope.$parent.iconClass = scope.iconClass;
                //var iconBtn = elem.find('a');
                //iconBtn..attr('class', "toolbar-icon "+iconClass);
                var childDir = elem.find('div');
                childDir.attr('filter-fields', scope.fieldVal);
                childDir.attr('filter-array-name', scope.fieldVal);
                childDir.attr('submit-label', scope.submitLabel);
                childDir.attr('clear-label', scope.clearLabel);
                childDir.attr('on-submit', scope.submitFunc);
                childDir.attr('on-clear', scope.clearFunc);
               // $compile(childDir.contents())(scope.$parent);
                $compile(elem.contents())(scope.$parent);
            }
            /**/
        };
    }])


    /*RESET BUTTON*/

    /**
     * @ngdoc directive
     * @name resetButton
     * @module toolbarButtons
     * @description 
     * Angular directive to show the reset button as a part of toolbar.
     * This directive belongs to the 'toolbarButtons' module and should be included in the app.js to be used within an application.
     * Reference to the handler function in the view's parent controller can be provided via setting the 'action' attribute.

     * @example <span reset-button action="resetFunction()"></span>
     * @see http://docs.angularjs.org/guide/directive
     *
     */

    .directive('resetButton', ['$rootScope','$compile', function($rootScope, $compile) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button tooltip="Refresh" class="btn btn-default btn-mini btn-toolbar" data-toggle="dropdown" ng-click="action()">\n\
                                <a class="toolbar-icon refresh"></a>\n\
                            </button>\n\
                        </div>',
                scope: {
                    action:'&'
                }
            }

        }])


    /*TOOLBAR BUTTON*/

    /**
     * @ngdoc directive
     * @name toolbarButton
     * @module toolbarButtons
     * @description 
     * Angular directive to show the button as a part of toolbar.
     * This directive belongs to the 'toolbarButtons' module and should be included in the app.js to be used within an application.
     * Reference to the handler function in the view's parent controller can be provided via setting the 'action' attribute.
     * This button can be used as a template to create toolbar button by providing a tooltip, icon and action.

     * @example <span toolbar-button tooltip-text="Click here" icon-class="someCssClass" action="someFunction()"></span>
     * @see http://docs.angularjs.org/guide/directive
     *
     */

    .directive('toolbarButton', ['$compile', function($compile) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button tooltip="{{tooltipText}}" class="btn btn-default btn-mini btn-toolbar" data-toggle="dropdown" ng-click="action()">\n\
                                <a class="toolbar-icon {{iconClass}}"></a>\n\
                            </button>\n\
                        </div>',
                scope: {
                    tooltipText:'@',
                    iconClass:'@',
                    action: '&'
                }
            }

        }])


    /*EXPORT BUTTON*/


    /* 
     * To change this template, choose Tools | Templates
     * and open the template in the editor.
     */

    .directive('exportButton', ['$compile', 'eventConstants', 
        function($compile, eventConstants) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button tooltip="Export" class="btn btn-default btn-mini btn-toolbar" data-toggle="dropdown" ng-click="action()">\n\
                                <a class="toolbar-icon export"></a>\n\
                            </button>\n\
                            <ul class="dropdown-menu toolbar-dropdown pull-right" role="menu">\n\
                                    <li><a href="#" ng-click="exportasXLS()"><img src="styles/img/toolbar_icons/spreadsheet.png"></img>  XLS</a></li>\n\
                            </ul>\n\
                        </div>',
                scope: {
                    action: '&'
                },
                controller: ['$scope', function($scope) {

               
                /**
                 * @ngdoc function
                 * @name exportasCSV
                 * @description 
                 * To export the grid data in CSV format.
                 * It broadcasts a global event 'exportasCSV' which can be handled anywhere on the top level.
                 * 
                 * @return {void}
                 */
               $scope.exportasCSV = function() {
                    $scope.$emit(eventConstants.EXPORT_AS_CSV);
                }; 


                /**
                 * @ngdoc function
                 * @name exportasXLS
                 * @description 
                 * To export the grid data in XLS format.
                 * It broadcasts a global event 'exportasXLS' which can be handled anywhere on the top level.
                 * 
                 * @return {void}
                 */ 

                $scope.exportasXLS = function() {
                    $scope.$emit(eventConstants.EXPORT_AS_XLS);
                };  

                 /**
                 * @ngdoc function
                 * @name exportasPDF
                 * @description 
                 * To export the grid data in PDF format.
                 * It broadcasts a global event 'exportasPDF' which can be handled anywhere on the top level.
                 * 
                 * @return {void}
                 */ 

                $scope.exportasPDF = function() {
                    $scope.$emit(eventConstants.EXPORT_AS_PDF);
                };  

            }]
            };

        }])


    /*FILE BUTTON*/

    /* 
     * To change this template, choose Tools | Templates
     * and open the template in the editor.
     */

    .directive('fileButton', ['$compile', 'eventConstants', 
        function($compile, eventConstants) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group" style="margin:0px;">\n\
                            <button tooltip="File Upload" class="btn btn-default btn-mini btn-toolbar filterBtn" data-toggle="dropdown" >\n\
                                <a class="toolbar-icon upload"></a>\n\
                            </button>\n\
                            <ul class="dropdown-menu toolbar-dropdown-filter pull-right" role="menu" style="padding:0px;">\n\
                                    <li>\n\
                                        <form class="fileUplaodBtn">\n\
                                            <div uploader ></div>\n\
                                        </form>\n\
                                    </li>\n\
                            </ul>\n\
                        </div>',
                scope: {
                    action: '&',
                    fileConfigUrl:'@',
                    fileUploadUrl:'@',
                    isMultipleFile:'@',
                    userId:'@',
                    monthList: '=',
                    yearList:'='
                },
                link: function(scope, elem, attrs) {
                    $compile(elem.contents())(scope.$parent);
                }
            };

        }])


    /*SAVE BUTTON*/


    /**
     * @ngdoc directive
     * @name saveButton
     * @module toolbarButtons
     * @description 
     * Angular directive to show the save button as a part of toolbar.
     * This directive belongs to the 'toolbarButtons' module and should be included in the app.js to be used within an application.
     * Reference to the handler function in the view's parent controller can be provided via setting the 'saveFunction' & 'saveAsFunction' attribute.

     * @example <span save-button save-function="someFunction()" save-as-function="someOtherFunction()"></span>
     * @see http://docs.angularjs.org/guide/directive
     *
     */

    .directive('saveButton', ['$compile', function($compile) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button tooltip="Save" class="btn btn-default btn-mini btn-toolbar" data-toggle="dropdown">\n\
                                <a class="toolbar-icon save"></a>\n\
                            </button>\n\
                            <ul class="dropdown-menu pull-right" role="menu">\n\
                                    <li><a href="#" ng-click="saveFunction()">1. Save</a></li>\n\
                                    <li><a href="#" ng-click="saveAsFunction()">2. Save as</a></li>\n\
                            </ul>\n\
                        </div>',
                scope: {
                    saveFunction: '&',
                    saveAsFunction: '&'
                    }
                }

        }])


    /*UPLOAD BUTTON*/

    /* 
     * To change this template, choose Tools | Templates
     * and open the template in the editor.
     */

    .directive('uploadButton', ['$compile', function($compile) {

            return{
                restrict: 'A',
                replace: true,
                template: '<div class="dropdown btn-group">\n\
                            <button tooltip="Upload" class="btn btn-default btn-mini btn-toolbar" data-toggle="dropdown" ng-click="action()">\n\
                                <i class="toolbar-icon glyphicon glyphicon-upload"></i>\n\
                            </button>\n\
                            <ul class="dropdown-menu pull-right" role="menu">\n\
                                    <li><a href="#" onclick="uploadasXLS()">1. XLS</a></li>\n\
                                    <li><a href="#" onclick="uploadasCSV()">2. CSV</a></li>\n\
                                    <li><a href="#" onclick="uploadasDelimited()">3. Delimited</a></li>\n\
                            </ul>\n\
                        </div>',
                scope: {
                    action: '&'
                },

                controller: ['$scope', function($scope) {

               
                /**
                 * @ngdoc function
                 * @name uploadasCSV
                 * @description 
                 * To upload the grid data in CSV format.
                 * It broadcasts a global event 'uploadasCSV' which can be handled anywhere on the top level.
                 * 
                 * @return {void}
                 */
               $scope.uploadasCSV = function() {

                    $rootScope.$broadcast(eventConstants.UPLOAD_AS_CSV);
                }; 


                /**
                 * @ngdoc function
                 * @name uploadasXLS
                 * @description 
                 * To upload the grid data in XLS format.
                 * It broadcasts a global event 'uploadasXLS' which can be handled anywhere on the top level.
                 * 
                 * @return {void}
                 */ 

                $scope.uploadasXLS = function() {

                    $rootScope.$broadcast(eventConstants.UPLOAD_AS_XLS);
                };  

                 /**
                 * @ngdoc function
                 * @name uploadasDelimited
                 * @description 
                 * To export the grid data in PDF format.
                 * It broadcasts a global event 'uploadasDelimited' which can be handled anywhere on the top level.
                 * 
                 * @return {void}
                 */ 

                $scope.uploadasDelimited = function() {

                    $rootScope.$broadcast(eventConstants.UPLOAD_AS_DELIMITED);
                };  

            }]
            };

        }]);

})();

